.time-analysis-page {
  display: flex;
  flex-direction: row;
  position: relative;
  height: 100%;
  width: 100%;
}
.time-analysis-page .time-analysis-middle {
  width: calc(100% - 308px);
  height: 100%;
}
.time-analysis-page .time-analysis-middle .csv-information {
  height: 60px;
  line-height: 60px;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.time-analysis-page .time-analysis-middle .time-analysis-wrapper {
  height: calc(100% - 60px);
  width: calc(100% - 15px);
  padding-top: 16px;
  margin-left: 6px;
  position: relative;
  background: #fff;
  box-shadow: 0 3px 10px 0 hsla(0, 0%, 64.7%, 0.5);
  border-radius: 8px;
  display: inline-block;
}
.time-analysis-page .time-analysis-middle .time-analysis-wrapper .show-model-selection {
  margin-left: 16px;
}
.time-analysis-page .time-analysis-middle .time-analysis-wrapper .time-seires-view {
  width: 100%;
  height: calc(100% - 225px);
  position: relative;
  border-bottom: 1px solid hsla(0, 0%, 64.7%, 0.5);
  overflow-y: scroll;
  overflow-x: scroll;
  z-index: 99;
}
.time-analysis-page .time-analysis-middle .time-analysis-wrapper .time-seires-view .focus-block {
  position: absolute;
  float: left;
  z-index: 3000;
  cursor: pointer;
  width: 100%;
}
.time-analysis-page .time-analysis-middle .time-analysis-wrapper .time-seires-view .ant-slider:hover .ant-slider-rail {
  background-color: #fff;
  opacity: 0;
}
.time-analysis-page .time-analysis-middle .time-analysis-wrapper .time-seires-view .ant-slider {
  height: 100%;
  margin: 0;
  padding: 0;
  z-index: 3000;
}
.time-analysis-page .time-analysis-middle .time-analysis-wrapper .time-seires-view .ant-slider-rail {
  height: 100%;
  opacity: 0.25;
  z-index: 3000;
  background-color: #fff;
}
.time-analysis-page .time-analysis-middle .time-analysis-wrapper .time-seires-view .ant-slider-track {
  height: 100%;
  border: 2px solid #f5bf27;
  opacity: 0.35;
  background-color: rgba(245, 191, 39, 0.55);
  z-index: 3000;
}
.time-analysis-page .time-analysis-middle .time-analysis-wrapper .time-seires-view .ant-slider-track:hover {
  height: 100%;
  border: 2px solid #f5bf27;
  opacity: 0.55;
  background-color: rgba(245, 191, 39, 0.55);
  z-index: 3000;
}
.time-analysis-page .time-analysis-middle .time-analysis-wrapper .time-seires-view .ant-slider-handle {
  height: 100%;
  margin-top: 0;
  cursor: col-resize;
  border-radius: 0%;
  opacity: 0.85;
  background-color: #f5bf27;
  width: 5px;
  z-index: 3000;
}
.time-analysis-page .time-analysis-middle .time-analysis-wrapper .time-sequence-view {
  width: 100%;
  display: block;
  height: 205px;
  position: absolute;
  bottom: 0;
}
.time-analysis-page .time-analysis-right {
  flex: 0 0 320px;
  max-width: 320px;
  min-width: 320px;
  width: 320px;
  position: relative;
}
